import React from 'react';
import './Method.css';
import structure from '../../images/structure.png';
import profession from '../../images/profession.png';
import qualification from '../../images/qualification.png';
import fgos from '../../images/fgos.png';

function Method() {

  return (
    <div className="method">
      <h1 className="main__title"> </h1>
      <ul className="method__list">
        <li className="method__item">
          <img className="method__item-img" alt="" src={structure}></img>
          <h3 className="method__item-title">  </h3>
          <button className="method__item-button" type="button"></button>
        </li>
        <li className="method__item">
          <img className="method__item-img" alt="" src={profession}></img>
          <h3 className="method__item-title"> </h3>
          <button className="method__item-button" type="button"></button>
        </li>
        <li className="method__item">
          <img className="method__item-img" alt="" src={qualification}></img>
          <h3 className="method__item-title">  &nbsp;</h3>
          <button className="method__item-button" type="button"></button>
        </li>
        <li className="method__item">
          <img className="method__item-img" alt="" src={fgos}></img>
          <h3 className="method__item-title">   </h3>
          <button className="method__item-button" type="button"></button>
        </li>
      </ul>
    </div>
  );
}

export default Method;